import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_INSERT_COL = Symbol('TABLE_INSERT_COL');

export function insertTableCol(
  table,
  colIndex: number,
  triggerColIndex: number,
  colWidth: number,
  containerWidth?: number,
) {
  return {
    type: TABLE_INSERT_COL,
    payload: {
      node: table,
      colIndex,
      colWidth,
      containerWidth,
      triggerColIndex,
    },
  };
}

interface TableInsertColPayload {
  node: Table;
  colIndex: number;
  colWidth: number;
  containerWidth?: number;
  triggerColIndex: number;
}

interface TableInsertColAction {
  type: typeof TABLE_INSERT_COL;
  payload: TableInsertColPayload;
}

export function isTableInsertColAction(
  action: Action,
): action is TableInsertColAction {
  return action.type === TABLE_INSERT_COL;
}
